"use client";

import * as React from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { YearSelector } from "@/components/year-selector";
import { getCurrentYear } from "@/lib/candy";

interface YearSelectorWrapperProps {
  selectedYear: number;
  availableYears?: number[];
}

export function YearSelectorWrapper({ 
  selectedYear, 
  availableYears 
}: YearSelectorWrapperProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const currentYear = getCurrentYear();

  // Generate list of past 5 years (current year + 5 previous years = 6 years total)
  const pastYears = React.useMemo(() => {
    const years: number[] = [];
    for (let i = 0; i <= 5; i++) {
      years.push(currentYear - i);
    }
    return years;
  }, [currentYear]);

  const handleYearChange = (year: number) => {
    const params = new URLSearchParams(searchParams.toString());
    params.set("year", year.toString());
    router.push(`/?${params.toString()}`);
  };

  return (
    <YearSelector 
      selectedYear={selectedYear} 
      onYearChange={handleYearChange}
      availableYears={pastYears}
    />
  );
}

