import { getDb } from "./db";

export interface Candy {
  id: number;
  candy_name: string;
  count: number;
  year: number;
  created_at: string;
  updated_at: string;
}

/**
 * Get the current year
 */
export function getCurrentYear(): number {
  return new Date().getFullYear();
}

/**
 * Get all candy counts, optionally filtered by year
 */
export function getAllCandies(year?: number): Candy[] {
  const db = getDb();
  if (year !== undefined) {
    const stmt = db.prepare("SELECT * FROM candy_counts WHERE year = ? ORDER BY candy_name");
    return stmt.all(year) as Candy[];
  }
  const stmt = db.prepare("SELECT * FROM candy_counts ORDER BY year DESC, candy_name");
  return stmt.all() as Candy[];
}

/**
 * Get all candies for a specific year
 */
export function getCandiesByYear(year: number): Candy[] {
  const db = getDb();
  const stmt = db.prepare("SELECT * FROM candy_counts WHERE year = ? ORDER BY candy_name");
  return stmt.all(year) as Candy[];
}

/**
 * Get all available years that have candy data
 */
export function getAvailableYears(): number[] {
  const db = getDb();
  const stmt = db.prepare("SELECT DISTINCT year FROM candy_counts ORDER BY year DESC");
  const results = stmt.all() as Array<{ year: number }>;
  return results.map((r) => r.year);
}

/**
 * Get a specific candy by name and year
 */
export function getCandyByName(candyName: string, year?: number): Candy | null {
  const db = getDb();
  const targetYear = year ?? getCurrentYear();
  const stmt = db.prepare("SELECT * FROM candy_counts WHERE candy_name = ? AND year = ?");
  const candy = stmt.get(candyName, targetYear) as Candy | undefined;
  return candy || null;
}

/**
 * Get a specific candy by ID
 */
export function getCandyById(id: number): Candy | null {
  const db = getDb();
  const stmt = db.prepare("SELECT * FROM candy_counts WHERE id = ?");
  const candy = stmt.get(id) as Candy | undefined;
  return candy || null;
}

/**
 * Increment candy count (creates if doesn't exist)
 */
export function incrementCandy(candyName: string, amount: number = 1, year?: number): Candy {
  const db = getDb();
  const targetYear = year ?? getCurrentYear();
  
  // Try to get existing candy
  const existing = getCandyByName(candyName, targetYear);
  
  if (existing) {
    // Update existing
    const stmt = db.prepare(
      "UPDATE candy_counts SET count = count + ? WHERE candy_name = ? AND year = ?"
    );
    stmt.run(amount, candyName, targetYear);
    // Fetch the updated record
    return getCandyByName(candyName, targetYear)!;
  } else {
    // Create new
    const stmt = db.prepare(
      "INSERT INTO candy_counts (candy_name, count, year) VALUES (?, ?, ?)"
    );
    stmt.run(candyName, amount, targetYear);
    // Fetch the newly created record
    return getCandyByName(candyName, targetYear)!;
  }
}

/**
 * Decrement candy count
 */
export function decrementCandy(candyName: string, amount: number = 1, year?: number): Candy | null {
  const db = getDb();
  const targetYear = year ?? getCurrentYear();
  const existing = getCandyByName(candyName, targetYear);
  
  if (!existing) {
    return null;
  }
  
  const newCount = Math.max(0, existing.count - amount);
  const stmt = db.prepare(
    "UPDATE candy_counts SET count = ? WHERE candy_name = ? AND year = ?"
  );
  stmt.run(newCount, candyName, targetYear);
  // Fetch the updated record
  return getCandyByName(candyName, targetYear)!;
}

/**
 * Set candy count to a specific value
 */
export function setCandyCount(candyName: string, count: number, year?: number): Candy {
  const db = getDb();
  const targetYear = year ?? getCurrentYear();
  const existing = getCandyByName(candyName, targetYear);
  
  if (existing) {
    const stmt = db.prepare(
      "UPDATE candy_counts SET count = ? WHERE candy_name = ? AND year = ?"
    );
    stmt.run(count, candyName, targetYear);
    // Fetch the updated record
    return getCandyByName(candyName, targetYear)!;
  } else {
    const stmt = db.prepare(
      "INSERT INTO candy_counts (candy_name, count, year) VALUES (?, ?, ?)"
    );
    stmt.run(candyName, count, targetYear);
    // Fetch the newly created record
    return getCandyByName(candyName, targetYear)!;
  }
}

/**
 * Delete a candy
 */
export function deleteCandy(candyName: string, year?: number): boolean {
  const db = getDb();
  const targetYear = year ?? getCurrentYear();
  const stmt = db.prepare("DELETE FROM candy_counts WHERE candy_name = ? AND year = ?");
  const result = stmt.run(candyName, targetYear);
  return result.changes > 0;
}

/**
 * Reset all candy counts to zero for a specific year
 */
export function resetAllCandies(year?: number): void {
  const db = getDb();
  if (year !== undefined) {
    db.prepare("UPDATE candy_counts SET count = 0 WHERE year = ?").run(year);
  } else {
    db.prepare("UPDATE candy_counts SET count = 0").run();
  }
}

