"use client";

import * as React from "react";
import { useRouter } from "next/navigation";
import { CandyTable } from "@/components/candy-table";
import type { Candy } from "@/lib/candy";

interface CandyListProps {
  initialCandies: Candy[];
  selectedYear?: number;
  currentYear?: number;
}

export function CandyList({ initialCandies, selectedYear, currentYear }: CandyListProps) {
  const router = useRouter();

  const handleUpdate = () => {
    // Refresh the page to get updated data
    router.refresh();
  };

  return (
    <CandyTable 
      candies={initialCandies} 
      onUpdate={handleUpdate}
      selectedYear={selectedYear}
      currentYear={currentYear}
    />
  );
}

